.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 0 auto 22.25px;
  display: block;
  object-fit: cover;
  cursor: pointer;
}

@media (min-width: 768px) {
  .profile__avatar {
    grid-column: 1;
    grid-row: 1/3;
  }
}

.profile__avatar:hover {
  opacity: 0.2;
}